import "./styles.css";
import {useState} from "react";

const replace = (input) => {
  return replaceLink(input)
  .replaceAll(/###/g, '#')
  .replaceAll(/\n\n/g, '\n')
  .replaceAll(/##/g, '\n##')
  .replaceAll(/## ([^\n]*)/g, '**$1**');
}

const replaceLink = (input) => {
  const newLink = 'https://viyna.net/';
  const reg = /(https:\/\/www\.notion\.so)\/(([^)]*))/gm;
  return input.replaceAll(reg, (match, link, path)=> {
      const pathParts = path.toLowerCase().split('-');
      const publishedPuth = pathParts.slice(0, pathParts.length - 1).join('-');
      return newLink + publishedPuth;
  });
}

export default function App() {
  const [getInput, setInput] = useState();
  const [getOutput, setOutput] = useState();
  return (
    <section>
      <label>
        Input: 
    <textarea value={getInput} onChange={e => setInput(e.target.value)}></textarea>
    </label>
    <button onClick={() => setOutput(replace(getInput))}>Replace</button>
    <label>
        output: 
        <textarea value={getOutput} onChange={setOutput}></textarea>
    </label>
    </section>
  );
}
